import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import fire from '../firebase/firebase'
const Login = () => {

    const [user, setUser] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");


    const clearInputs = () => {
        setEmail("");
        setPassword("");
    };


    const handleLogin = () => {
        fire
            .auth().createUserWithEmailAndPassword(email, password)
            .catch(err => console.log(err))
        clearInputs();
    };





    return (
        <div className='login__container'>
            <form onSubmit={handleLogin}>
                <input
                    className='booklist_input w100'
                    type="text"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="Email"
                    required
                />

                <input
                    className='booklist_input w100'
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Password"
                    required
                />
                
                {
                    !password || !email ? (

                    <Link to='/'>
                        <button
                            className='login__button-disable'
                            type="disable"
                        >
                            SAVE
                        </button>
                    </Link>
                    ) : (
                        <Link to='/main/search'>
                            <button
                                onClick={handleLogin}
                                className={!email && !password ? 'login__button-disable' : 'login__button'}
                                type="submit"
                            >
                                SAVE
                            </button>
                        </Link>
                    )
                }
                <p>If you don't want to create an account,<Link to="/main/search"> <span className="login__span">please click here</span></Link></p>
            </form>
        </div>
    )
};


export default Login;